.main-nav {
  background: #fff;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.03), 0 0 0 1px rgba(231, 231, 231, 0.5);
}

.main-nav__container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1320px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
  align-items: center;
}

@media screen and (min-width: 65em) {
  .main-nav__container {
    flex-wrap: no-wrap;
  }
}

.main-nav__logo-wrapper {
  padding: 1rem;
  padding-left: 0.5rem;
}

@media screen and (min-width: 65em) {
  .main-nav__logo-wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.main-nav__list {
  display: none;
}

@media screen and (min-width: 65em) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    flex: 1;
    padding-left: 1rem;
  }
}

@media screen and (min-width: 65em) {
  .main-nav__item {
    display: inline-block;
    position: relative;
    margin-left: 2rem;
    overflow: hidden;
  }

  .main-nav__item::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 5px solid #6ca2d5;
    border-radius: 1px 1px 0 0;
    transform: translateY(100%);
    transition: transform 0.1s ease-in-out;
  }

  .main-nav__item.active::after {
    border-bottom: 5px solid #0e5aa4;
    transform: translateY(0);
  }

  .main-nav__item:hover::after {
    transform: translateY(0);
  }
}

.main-nav__link {
  display: block;
  color: #000;
  font-weight: bold;
  font-size: 0.9375rem;
  padding: 1.5rem 0;
}

.main-nav__utilities {
  display: inline-flex;
  align-items: center;
}

@media screen and (min-width: 65em) {
  .main-nav__utilities {
    order: 3;
  }
}

.main-nav__icon-btn {
  display: inline-flex;
  margin-left: 0.25rem;
}

.icon-button {
  height: 40px;
  width: 40px;
  border: 1px solid #e9e9e9;
  border-radius: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: background 0.1s ease-in-out;
}

.icon-button:hover {
  background: #e9e9e9;
}

@media screen and (min-width: 65em) {
  .main-nav__menu-btn {
    display: none;
  }
}

.main-nav__show-more-btn {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
}

.dropdown {
  display: none;
}

.dropdown.is-open {
  display: flex;
}

@media screen and (min-width: 65em) {
  .dropdown {
    display: none;
  }

  .dropdown.is-open {
    display: none;
  }
}

.dropdown__container {
  width: 100%;
}

.dropdown__header {
  display: none;
  margin-bottom: 1rem;
}

.dropdown__list {
  display: block;
  width: 100%;
  border-bottom: 1px solid #e4e4e4;
}

.dropdown__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid #e9e9e9;
}

.dropdown__item.is-expanded > .dropdown__sublist {
  display: block;
}

.dropdown__item:last-of-type {
  border-bottom: none;
}

.dropdown__item:not(.has-sublist) > .dropdown__link {
  font-weight: 400;
}

.dropdown__item .dropdown__sublist {
  display: none;
}

.dropdown__link {
  display: flex;
  flex: 1;
  font-weight: bold;
  color: #000;
  padding: 1rem 1rem;
}

.dropdown__sublist {
  display: none;
  width: 100%;
  border-top: 1px solid #e4e4e4;
  border-left: 0.5rem solid #cee2f2;
  background: #f8f8f8;
}

.dropdown__sublist.is-open {
  display: block;
}

.dropdown__toggle-btn {
  background: #aecfe9;
  border-radius: 50%;
  margin-right: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
}

.dropdown__toggle-btn svg {
  fill: #0e5aa4;
}

.breadcrumbs-container {
  display: none;
}

@media screen and (min-width: 65em) {
  .breadcrumbs-container {
    display: block;
    background: rgba(108, 162, 213, 0.1);
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #e4e4e4;
  }
}

.section-header {
  background: rgba(108, 162, 213, 0.1);
  padding: 2rem 0 0 0;
}

.section-header__title {
  margin-bottom: 1rem;
}

.section-nav__list {
  display: flex;
  width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
}

.section-nav__item {
  margin-right: 1rem;
}

.section-nav__link {
  display: block;
  position: relative;
  padding: 1rem 0;
  font-weight: bold;
}

.section-nav__link::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 5px solid #6ca2d5;
  border-radius: 1px 1px 0 0;
  transform: translateY(100%);
  transition: transform 0.1s ease-in-out;
}

.section-nav__link.active::after {
  border-bottom: 5px solid #0e5aa4;
  transform: translateY(0);
}

.section-nav__link:hover::after {
  transform: translateY(0);
}

.alternation:nth-child(even) {
  background-color: #fafafa;
}

p.light {
  font-family: 'Lato';
  color: #3e3e3e;
  font-size: 0.9em;
}

.alignright {
  float: right;
  margin: 0 0 1em 1em;
}

.alignleft {
  float: left;
  margin: 0 1em 1em 0;
}

.comments li {
  background: #fff;
  border: 1px solid #e9e9e9;
  margin-bottom: 2rem;
}

.comments li .comment-body {
  padding: 1rem;
}

.comments li .children li {
  border: none;
  border-top: 1px solid #e9e9e9;
  background: #f8fbfc;
  margin-bottom: 0;
}




